button:hover {
    cursor: pointer;
}
button:focus {
    outline: none;
}
hr {
    border: 1px solid white;
    border-radius: 50px;
}
.App {
    display: flex;
    flex-direction: column;
}

.headerContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;
    width: 900px;
    align-self: center;
}
.headerBtn {
    background-color: white;
    border: none;
    height: 40px;
    width: 90px;

}
.logInBtn {
    background-color: #355c7d;
    color: white;
    border: none;
    height: 40px;
    width: 100px;
    border-radius: 50px;
}
.logo {
    color: black;
    background-color: white;
    border: none;
    text-decoration: none;
}
.footerlogo {
    background-color: #355c7d;
    color: white;
    border: none;
    text-decoration: none;
}
.Home {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.welcomeContainer {
    margin-bottom: 50px;
    display: grid;
    grid-template-columns: 50% 50%;
    width: 900px;
    align-self: center;
}

.welcomeRight {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.sectionOneContainer {
    background-color: #f0f3f5;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.sectionOne {
    width: 900px;
    align-self: center;
    display: grid;
    grid-template-columns: 50% 50%;
    padding-top: 50px;
    padding-bottom: 50px;
}

.sectionTwoContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.sectionTwo {
    width: 900px;
    align-self: center;
    align-self: center;
    display: grid;
    grid-template-columns: 50% 50%;
    padding-top: 50px;
    padding-bottom: 50px;
}


.footerContainer {
    background-color: #355c7d;
    display: flex;
    flex-direction: column;
}
.footer {
    width: 900px;
    color: white;
    margin-top: 20px;
    margin-bottom: 50px;
    align-self: center;
}

.footerBtn {
    background-color: #355c7d;
    color: white;
    border: none;
}
.footerBtnContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;
}

.footetParagraph {
    text-align: center;
}


.center {
    display: flex;
    flex-direction: column;
    justify-content: center;
}


.About, .Tools {
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-self: center; */
}

.aboutSection, .toolsContainer {
    width: 900px;
    align-self: center;
}

.aboutSectionContainer1 {
    background-color: #f0f3f5;
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    padding-bottom: 50px;
}

.aboutSectionContainer2 {
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    padding-bottom: 50px;
}

.numbered {
    border: 1px solid #355c7d;
    border-radius: 50px;
    width: 45px;
    height: 45px;
    background-color: #355c7d;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.watchContainer {
    /* border: 1px solid red; */
    width: 900px;
    display: flex;
    flex-direction: column;
    align-self: center;
}

.watchMapSection {
    display: grid;
    grid-template-columns: 33.3% 33.3% 33.3%;
    /* grid-gap: 10px; */
}

.watchMapContainer {
    border: 1px solid lightgray;
    width: 280px;
    height: 360px;
    margin-bottom: 50px;
    padding: 5px;

    display: grid;
    grid-template-rows: 80% 20%;
}

.watchMap {
    background-color: #f0f3f5;
}

.Create {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.searchInputContainer {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.searchBtn {
    background-color: #355c7d;
    /* color: white; */
    border: none;
    height: 40px;
    width: 40px;
    border-radius: 50px;
}

.searchInput {
    height: 40px;
    width: 300px;
    /* color: lightgray; */
    padding-left: 20px;
    font-size: 20px;
    margin-right: 10px;
    border-radius: 50px;
    border: 1px solid lightgray;
}

.searchInput:focus {
    outline: none;
}

.Upload, .Help{
    display: flex;
    flex-direction: column;
    justify-content: center;
}